import Layout from "./layout"
import { Helmet } from "react-helmet"
import Grid from "@material-ui/core/Grid"
import Profile from "./profile"
import React from "react"

class Project extends React.Component{

  constructor ({Info}){
    super();
    this.Info = Info;

    this.state = {
      width: 0,
      box_xs: 12,
      box_xs2: 12,
    };
  }

  componentDidMount() {
    this.handleWindowSizeChange()
    window.addEventListener('resize', this.handleWindowSizeChange);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }
  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  render(){
    const Info = this.Info;
    const {width} = this.state;
    const isMobile = width <= 720;
    if (isMobile || Info[0].contacts.length === 0){
      this.box_xs = 12;
      this.box_xs2 = 12;
    } else {
      this.box_xs = 7;
      this.box_xs2 = 5;
    }

    return (
      <div>
        {Info.map((info) => (
          <Layout>
            <Helmet>
              <title>{info.title} | Jan Garong</title>
            </Helmet>
            <div style={{backgroundColor: '#393e46', paddingTop: '50px', paddingBottom: '50px'}}>
              <Grid container justify={'center'} style={{paddingLeft: '10vw', paddingRight: '10vw'}}>
                <Grid item xs={this.box_xs} justify={'center'}>
                  <div style={{marginBottom: '25px'}}>
                    <h1>{info.title} </h1>
                    <p style={{fontSize: '80%'}}>{info.date}</p>
                    {info.desc}
                  </div>
                </Grid>
                <Grid container spacing={2} justify={'center'} alignItems={'center'} xs={this.box_xs2}>
                  {info.contacts.map((profile) => (<Profile name={profile.name} img={profile.img}
                                                            github={profile.github}
                                                            linkedin={profile.linkedin}
                                                            url={profile.url}/>))}
                </Grid>
              </Grid>
            </div></Layout>))}
      </div>
    );
  }
}

export default Project;