import React from "react";
import Grid from '@material-ui/core/Grid';
import { Image } from "react-bootstrap";
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import LinkIcon from '@material-ui/icons/Link';

function display_icon(url, Icon, width, height){
  if (url) {
    return (
      <a href={url} style={{marginLeft: '7px', marginRight: '7px'}}>
        <Icon style={{width: width, height: height}} />
      </a>);
  }
}

class Profile extends React.Component{

  constructor ({name = '',
                 img,
                 pfp_size='64px',
               github='',
               linkedin='',
               url=''}){
    super();
    this.name = name;
    this.img = img;
    this.pfp_size = pfp_size;
    this.github = github;
    this.linkedin = linkedin;
    this.url = url;

    this.state = {
      width: 0,
      box_xs: 12,
    };
  }

  componentDidMount() {
    this.handleWindowSizeChange()
    window.addEventListener('resize', this.handleWindowSizeChange);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }
  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  render(){
    const name = this.name;
    const img = this.img;
    const pfp_size = this.pfp_size;
    const github = this.github;
    const linkedin = this.linkedin;
    const url = this.url;

    const gh_icon = display_icon(github, GitHubIcon, '24px', '24px');
    const li_icon = display_icon(linkedin, LinkedInIcon, '24px', '24px');
    const url_icon = display_icon(url, LinkIcon, '24px', '24px');

    const {width} = this.state;
    const isMobile = width <= 1800;
    if (isMobile){
      this.box_xs = 6;
    } else {
      this.box_xs = 6;
    }

    return(
        <Grid item>
          <div align={'center'} style={{paddingTop:'20px'}}>
            <Image style={{
              width: pfp_size,
              height: pfp_size,
              marginBottom: '10px',
            }} src={img} roundedCircle/>
            <div style={{fontSize: '125'}}>
              {name}
            </div>
            <div align={'center'} style={{paddingTop: '10px', paddingBottom: '10px'}}>
              {li_icon}
              {gh_icon}
              {url_icon}
            </div>
          </div>
        </Grid>
    );
  }
}



export default Profile